import React, {useState} from "react";
import views from "../img/views.png";


const EventForm = ({Data, setData}) => {
    const [view, setView] = useState(0);

    const clickView =  (e) => {
        setView(view + 1);
    };
    //      https://github.com/kolodny/immutability-helper 
    // target_blank    https://beomsoo.me/issue/Issue-4/

    return (
    <> {
       Data.map((i, index) => (
            <a href="#" onClick={clickView} rel="noopener noreferrer" key={index} id={i.idx}>
                <div className="evtForm_box" >
                    <div className="evtForm_img"><img src={i.img} alt="img"/></div>
                    <p className="evtForm_date">{i.date}</p>
                    <p className="evtForm_views"><img src={views} alt=""/>{view}</p>
                    <h1 className="evtForm_title">{i.title}</h1>
                </div>
            </a>
        ))
    } 
    </>
    )
};

export default React.memo(EventForm);