import React,{useState,useEffect,useRef} from "react";
import logoSVG from "../img/saemoiSVG2.svg"
import { Link } from "react-router-dom";
import "../css/header.css"
import { CgMenu} from "react-icons/cg";
import { GrFormClose } from "react-icons/gr";







const Header = () => {


    const [visible, setVisible] = useState(false);

    const [hide, setHide] = useState(false);
    const [pageY, setPageY] = useState(0);

    const [viewEvent, setViewEvent] = useState("");

    const documentRef = useRef(document);

    
const throttle = function (callback, waitTime) {
    let timerId = null;
    return (e) => {
        if (timerId) return;
        timerId = setTimeout(() => {
            callback.call(this, e);
            timerId = null;
        }, waitTime);
    };
};
const handleScroll = () => {
    const { pageYOffset } = window;
    const deltaY = pageYOffset - pageY;
    const hide = pageYOffset !== 0 && deltaY >= 0;
    const hide2 = pageYOffset === 0 && deltaY >= 0;

    setVisible(hide2);
    setHide(hide);
    setPageY(pageYOffset);
};
const throttleScroll = throttle(handleScroll, 50);

    useEffect(() => {
       const documentRefCopy = documentRef;
        documentRefCopy.current.addEventListener('scroll', throttleScroll);
        return () => documentRefCopy.current.removeEventListener('scroll', throttleScroll);
    }, [pageY]);

    const toggleMenu = () => {
        setVisible(visible => !visible);
    };

    const changeMenu = (e) => {
        console.log(e.target.innerText);

        setViewEvent(e.target.innerText);
        setVisible(false);
    }

    const goHome = () =>{
        setViewEvent("")
    }


    return(
        <>
        <header>
            <div className={hide ? 'headerWrap hide' : 'headerWrap'}>
                <Link to="/"><div className="logo" onClick={goHome}><img src={logoSVG} alt="logo"/></div></Link>
            <div className="btnHome" onClick={toggleMenu}>{visible ? <GrFormClose size={30} className="closeBtn"/> :  <CgMenu size={30}/> }</div>
            {/* <div className={visible ? "headerSubSlide active" : "headerSubSlide hide"}></div> */}
            </div>

            
            <div className={visible ? "headerSlide active" : "headerSlide hide"}>
                <ul>
                    <Link to="/" onClick={changeMenu}><li></li></Link>
                    <Link to="olive" onClick={changeMenu}><li></li></Link>
                    <Link to="mustit" onClick={changeMenu}><li></li></Link>
                    <Link to="about" onClick={changeMenu}><li></li></Link>

                </ul>
                
            </div>
       
        
        </header>

        <div className="viewEvent">{viewEvent}</div>
        </>
    )


}

export default Header;